import { useMutation, useQueryClient } from '@tanstack/react-query';
import { isTonAsset } from '@tonkeeper/core/dist/entries/crypto/asset/asset';
import { TON_ASSET } from '@tonkeeper/core/dist/entries/crypto/asset/constants';
import { sendJettonTransfer } from '@tonkeeper/core/dist/service/transfer/jettonService';
import { sendTonTransfer } from '@tonkeeper/core/dist/service/transfer/tonService';
import { notifyError } from '../../components/transfer/common';
import { getSigner } from '../../state/mnemonic';
import { useWalletJettonList } from '../../state/wallet';
import { useTransactionAnalytics } from '../amplitude';
import { useAppContext, useWalletContext } from '../appContext';
import { useAppSdk } from '../appSdk';
import { useTranslation } from '../translation';
import { useCheckTouchId } from '../../state/password';
export function useSendTransfer(recipient, amount, isMax, estimation) {
    const { t } = useTranslation();
    const sdk = useAppSdk();
    const { api } = useAppContext();
    const wallet = useWalletContext();
    const client = useQueryClient();
    const track2 = useTransactionAnalytics();
    const { data: jettons } = useWalletJettonList();
    const { mutateAsync: checkTouchId } = useCheckTouchId();
    return useMutation(async () => {
        const signer = await getSigner(sdk, wallet.publicKey, checkTouchId).catch(() => null);
        if (signer === null)
            return false;
        try {
            if (isTonAsset(amount.asset)) {
                if (amount.asset.id === TON_ASSET.id) {
                    track2('send-ton');
                    await sendTonTransfer(api, wallet, recipient, amount, isMax, estimation.payload, signer);
                }
                else {
                    track2('send-jetton');
                    const jettonInfo = jettons.balances.find(jetton => amount.asset.address.toRawString() ===
                        jetton.jetton.address);
                    await sendJettonTransfer(api, wallet, recipient, amount, jettonInfo.walletAddress.address, estimation.payload, signer);
                }
            }
            else {
                throw new Error('Disable trc 20 transactions');
                // track2('send-trc20');
                // await sendTronTransfer(
                //     {
                //         tronApi: api.tronApi,
                //         tron: wallet.tron!,
                //         request: (estimation.payload as EstimatePayload).request
                //     },
                //     {
                //         mnemonic
                //     }
                // );
            }
        }
        catch (e) {
            await notifyError(client, sdk, t, e);
        }
        await client.invalidateQueries({
            predicate: query => query.queryKey.includes(wallet.active.rawAddress)
        });
        return true;
    });
}
