import { useQuery, useQueryClient } from '@tanstack/react-query';
import { isTonAsset } from '@tonkeeper/core/dist/entries/crypto/asset/asset';
import { AssetAmount } from '@tonkeeper/core/dist/entries/crypto/asset/asset-amount';
import { TON_ASSET } from '@tonkeeper/core/dist/entries/crypto/asset/constants';
import { estimateJettonTransfer } from '@tonkeeper/core/dist/service/transfer/jettonService';
import { estimateTonTransfer } from '@tonkeeper/core/dist/service/transfer/tonService';
import { estimateTron } from '@tonkeeper/core/dist/service/tron/tronTransferService';
import { notifyError } from '../../components/transfer/common';
import { QueryKey } from '../../libs/queryKey';
import { DefaultRefetchInterval } from '../../state/tonendpoint';
import { useTronBalances } from '../../state/tron/tron';
import { useWalletJettonList } from '../../state/wallet';
import { useAppContext, useWalletContext } from '../appContext';
import { useAppSdk } from '../appSdk';
import { useTranslation } from '../translation';
async function estimateTon({ recipient, amount, isMax, api, wallet, jettons }) {
    let payload;
    if (amount.asset.id === TON_ASSET.id) {
        payload = await estimateTonTransfer(api, wallet, recipient, amount.weiAmount, isMax);
    }
    else {
        const jettonInfo = jettons.balances.find(jetton => amount.asset.address.toRawString() === jetton.jetton.address);
        payload = await estimateJettonTransfer(api, wallet, recipient, amount, jettonInfo.walletAddress.address);
    }
    const fee = new AssetAmount({ asset: TON_ASSET, weiAmount: payload.event.extra * -1 });
    return { fee, payload };
}
export function useEstimateTransfer(recipient, amount, isMax) {
    const { t } = useTranslation();
    const sdk = useAppSdk();
    const { api } = useAppContext();
    const wallet = useWalletContext();
    const client = useQueryClient();
    const { data: jettons } = useWalletJettonList();
    const { data: balances } = useTronBalances();
    return useQuery([QueryKey.estimate, recipient, amount], async () => {
        try {
            if (isTonAsset(amount.asset)) {
                return await estimateTon({
                    amount: amount,
                    api: api,
                    wallet,
                    recipient,
                    isMax,
                    jettons
                });
            }
            else {
                return await estimateTron({
                    amount: amount,
                    tronApi: api.tronApi,
                    wallet,
                    recipient,
                    isMax,
                    balances
                });
            }
        }
        catch (e) {
            await notifyError(client, sdk, t, e);
            throw e;
        }
    }, {
        refetchInterval: DefaultRefetchInterval,
        refetchOnMount: 'always'
    });
}
